.search-calendar {
  width: 200px;
  height: 300px;
  padding-bottom: 10px;
  text-align: left;
}
.search-calendar__date {
  text-align: left;
}
.search-calendar__date-box {
  display: flex;
  flex-direction: row;
}
.search-calendar__date-month {
  align-self: flex-end;
}
.search-calendar__title {
  padding-bottom: 10px;
}
.search-calendar__invitees {
  align-self: flex-end;
  display: flex;
  flex-direction: row;
  height: 50px;
}
.search-calendar__box {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}
.search-calendar_invitees-item {
  font-size: larger;
  font-weight: 500;
  height: 40px;
  width: 40px;
  margin: 3px 3px 3px 3px;
  background-color: #415aff;
  text-align: center;
}
.search-file {
  width: 850px;
  height: 50px;
  padding-bottom: 10px;
  text-align: left;
  margin-bottom: 20px;
}
.search-file__box {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  align-items: center;
}
.search-file__type {
  padding-left: 15px;
  padding-right: 5px;
}
.search-file__box {
  padding-left: 5px;
}
.search-file__date-box {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  align-items: center;
  justify-content: flex-end;
  width: 350px;
  margin-right: 50px;
  margin-left: 10px;
}
.search-file_users-item {
  font-size: larger;
  font-weight: 500;
  height: 20px;
  width: 20px;
  margin: 3px 3px 3px 3px;
  background-color: #415aff;
  text-align: center;
}
.search-file__date-month {
  font-size: 5px;
  margin-left: 5px !important;
  margin-right: 8px;
}
.search-file__date-date {
  margin-right: 5px;
  width: 20px;
  margin-left: 15px;
  text-align: end;
}
.search-file__user-box {
  margin-right: 10px;
}
.search-contact__name {
  margin-left: 10px;
}
.search-contact__name-highlight {
  margin-left: 10px;
  color: #f50057;
}
.search-contact__email {
  margin-top: 10px;
  margin-left: 3px;
}
.search-contact__email-highlight {
  margin-top: 10px;
  margin-left: 3px;
  color: #f50057;
}
.search-contact__sybmol {
  margin-left: 10px;
}
.search-contact__email-box {
  width: 50%;
}
.search-contact {
  width: 300px;
  margin-bottom: 20px;
  min-height: 130px;
}
.search-contact_users-item {
  font-size: 40px;
}
.search-contact_users-item-secondary {
  color: #f50057;
  font-size: 40px;
}
.search-contact__box-top {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  align-items: center;
  height: 65px;
}
.search-contact__box {
  display: flex;
  flex-direction: column;
}
.search-contact__email-list {
}
.search-contact__number-list {
  display: flex;
  flex-direction: column;
}
.search-contact__number-box {
  /* display: flex;
  flex-direction: row;
  align-self: flex-start;
  margin-left: 7px;
  margin-top: 7px;
  align-content: flex-start; */
  width: 50%;
}
.search-contact__phone {
  margin-top: 5px;
}
.search-contact__date {
  justify-self: flex-end;
  font-size: 40px;
  display: flex;
  align-items: baseline;
  margin-left: auto;
}
.search-contact__date-month {
  font-size: 1rem;
  color: #808080;
  /* margin-top: auto; */
}
.search-message {
  width: 850px;
  height: 50px;
  padding-bottom: 10px;
  text-align: left;
  margin-bottom: 20px;
}
.new {

}

.search-message__channel-box {
  margin-right: 10px;
  font-size: 12px;
}
.search-message__date-date {
  margin-right: 5px;
  width: 20px;
  margin-left: 15px;
  text-align: end;
  width: fit-content;
}
.search-file__date-box {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  align-items: center;
  justify-content: flex-end;
  width: 450px;
  margin-right: 50px;
  margin-left: 10px;
}
.search-message__box {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  align-items: center;
  font-size: 12px;
}
